import { BaseCrudService } from "../base-crud.service";
import * as Axios from 'axios';
import {BASE_API_URL} from "env";

export default class CartonOutService extends BaseCrudService{
  constructor(token)
  {
    super("carton-out", token);
  }

  createBulkCartons(numCartons, whOutId)
  {
    return Axios.post(`${BASE_API_URL}/${this._baseUrl}/create/bulk`, {numCartons: numCartons, whOpId: whOutId}, this._headers)
  }
}