import { BaseCrudService } from "../base-crud.service";
import * as Axios from 'axios';
import {BASE_API_URL} from "env";

export default class CartonInService extends BaseCrudService
{
  constructor(token)
  {
    super("carton-in", token);
  }

  createBulkCartons(numCartons, whInId)
  {
    return Axios.post(`${BASE_API_URL}/${this._baseUrl}/create/bulk`, {numCartons: numCartons, whOpId: whInId}, this._headers)
  }
}