import * as Axios from 'axios';
import {BASE_API_URL} from "env";

export class BaseCrudService{

  constructor(baseUrl, jwtToken)
  {
    this._baseUrl = baseUrl;
    this._headers = { headers: {Authorization: `Bearer ${jwtToken}`} };
  }

  create(newEntity)
  {
    return Axios.post(`${BASE_API_URL}/${this._baseUrl}`, newEntity, this._headers);
  }

  createBulk(entities)
  {
    return Axios.post(`${BASE_API_URL}/${this._baseUrl}/bulk`, entities, this._headers);
  }

  read(entityId, queryParams)
  {
    let url = `${BASE_API_URL}/${this._baseUrl}/${entityId}`;

    if (queryParams)
      url = `${url}?${queryParams}`;

    return Axios.get(`${BASE_API_URL}/${this._baseUrl}/${entityId}`, this._headers);
  }

  readAll(queryParams)
  {
    let url = `${BASE_API_URL}/${this._baseUrl}`;

    if (queryParams)
      url = `${url}?${queryParams}`;

    return Axios.get(url, this._headers);
  }

  update(entityId, updateData)
  {
    return Axios.patch(`${BASE_API_URL}/${this._baseUrl}/${entityId}`, updateData, this._headers);
  }

  delete(entityId)
  {
    return Axios.delete(`${BASE_API_URL}/${this._baseUrl}/${entityId}`, this._headers);
  }
}